.header {
  display: inline-block;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px 10% 0;
  max-height: 150px; }

.header__logo, .header__contacts {
  margin: 0 10px; }

.header__logo {
  float: left;
  background: url(../../images/logo.png) no-repeat;
  background-size: 150px;
  width: 150px;
  height: 65px; }

.header__contacts {
  width: 220px;
  height: 50px;
  float: right;
  text-align: center; }

.header__contacts--link {
  display: inline-block;
  font-size: 22px;
  color: #333;
  text-decoration: none;
  vertical-align: top;
  padding-top: 5px; }

.header__contacts--link:hover {
  color: #333;
  text-decoration: none; }

.header__contacts--icon {
  vertical-align: top;
  display: inline-block;
  min-width: 40px;
  min-height: 40px;
  padding: 7px;
  background-color: #fff;
  border-radius: 50%;
  border: 2px solid;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #00AB4F;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg); }

.header__contacts--call {
  vertical-align: top;
  display: inline-block;
  max-width: 80%;
  padding: 0 5px 5px; }

.header__contacts--text {
  float: right;
  font-size: 12px; }

.header__contacts--phone {
  float: right;
  font-size: 19px; }

@media (max-width: 900px) {
  .header__contacts--call {
    display: none; }
  .header__contacts {
    width: auto; } }



